import React from "react"
import styles from "./cartoon.module.sass"
import Img from "gatsby-image"
import triangle from "../flowChart/timeline-triangle.svg"

class CartoonStrip extends React.PureComponent {
  render() {
    const { images } = this.props
    return (
      <div className={styles.cartoonWrapper}>
        <div className={styles.headings}>
          <h2>Life With KintoHub</h2>
          <h2>Life Without KintoHub</h2>
        </div>
        <ul className={styles.inner}>
          {images.map((image, index) => (
            <li key={index}>
              <div className={[styles.time, styles.mobileOnly].join(" ")}>
                <h3>{image.time}am</h3>
              </div>
              <div className={styles.imageAndText}>
                <Img fluid={image.left.image} className={styles.image} />
                <h5 className={styles.text}>{image.left.text}</h5>
              </div>
              <div className={[styles.center, styles.desktopOnly].join(" ")}>
                <div className={styles.time}>
                  <h3>{image.time}am</h3>
                  <div className={styles.line} />
                  <img src={triangle} alt="" className={styles.triangle} />
                  <img src={triangle} alt="" className={styles.triangle} />
                  <img src={triangle} alt="" className={styles.triangle} />
                </div>
              </div>
              <div className={styles.imageAndText}>
                <Img fluid={image.right.image} className={styles.image} />
                <h5 className={styles.text}>{image.right.text}</h5>
              </div>
            </li>
          ))}
        </ul>
      </div>
    )
  }
}
export default CartoonStrip
